import { createStore } from 'vuex'

export default createStore({
  state: {
    currCity:"臺北市",
    currDistrict:"北投區",
    location:[],
    stores:[],
    keywords:'',
    showModal:false,
    infoBoxSid: null
  },
  getters:{
    cityList(state){
      return state.location.map(c=>c.name);
    },
    districtList(state){
      let newArr = state.location.find((c) => c.name === state.currCity);
      if(newArr === undefined){
        return [];
      }
      return newArr.districts;
    },
    filteredStores(state){
      const {stores} = state;
      return state.keywords
      ?stores.filter(s => s.name.includes(state.keywords))
      :stores.filter(s => s.county === state.currCity && s.town === state.currDistrict);
    },
    currDistrictInfo(state,getters){
      return getters.districtList.find(d=>d.name === state.currDistrict)||{};
    }
  },
  mutations: {
    setcurrCity(state,payload){
      state.currCity = payload;
    },
    setcurrDistrict(state,payload){
      state.currDistrict = payload;
    },
    setlocation(state,payload){
      state.location = payload;
    },
    setStores(state,payload){
      state.stores = payload;
    },
    setKeywords(state,payload){
      state.keywords = payload;
    },
    setshowModal(state,payload){
      state.showModal = payload;
    },
    setinfoBoxSid(state,payload){
      state.infoBoxSid = payload;
    }
  },
  actions: {
    async fetchLocations({commit}){
      const json = await fetch('https://raw.githubusercontent.com/kurotanshi/mask-map/master/raw/area-location.json').then(res=>res.json());
      commit('setlocation',json);
    },
    async fetchPharmacies({commit}){
      const json = await fetch('https://kiang.github.io/pharmacies/json/points.json').then(res=>res.json());
      const data = json.features.map(d=> {
        return ({
          ...d.properties,
          latitude: d.geometry.coordinates[0],
          longitude: d.geometry.coordinates[1]
        })
      })
      commit('setStores',data);
    }
  },
  modules: {
  }
})
