<template>
<!-- aside-menu 左側欄 -->
<div class="aside-menu">
    <div class="wraps">
    <label>
        縣市：
        <select v-model="currCity">
            <option v-for="c in cityList" :key="c">{{c}}</option>
        </select>
    </label>
    <label>
        行政區：
        <select v-model="currDistrict">
            <option v-for="d in districtList" :key="d.id">{{d.name}}</option>
        </select>
    </label>
    </div>

    <div class="wraps">
    <label>
        <i class="fas fa-search-location"></i> 關鍵字搜尋：
        <input v-model="keywords" type="text" placeholder="請輸入關鍵字">
    </label>
    </div>

    <ul class="store-lists">
        <li class="store-info wraps" v-for="s in filteredStores" :key="s.id" @click="$emit('triggerMarkerPopup', s.id)">
            <h1 v-html="keywordHighlight(s.name)"></h1>

            <div class="mask-info">
                <i class="fas fa-head-side-mask"></i>
                <span>大人口罩: {{s.mask_adult}} 個</span>
            </div>

            <div class="mask-info">
                <i class="fas fa-baby"></i>
                <span>兒童口罩: {{s.mask_child}} 個</span>
            </div>

            <div class="mask-info">
                最後更新時間: {{s.updated}}
            </div>

            <button class="btn-store-detail" @click="openInfo(s.id)">
                <i class="fas fa-info-circle"></i>
                看詳細資訊
            </button>
        </li>
    </ul>
</div>
</template>

<script>
import {mapGetters,mapState} from 'vuex';
export default {
name: 'asideMenu',
computed:{
    currCity:{
        get(){
            return this.$store.state.currCity;
        },
        set(val){
            this.$store.commit('setcurrCity',val);
        }
    },
    currDistrict:{
        get(){
            return this.$store.state.currDistrict;
        },
        set(val){
            this.$store.commit('setcurrDistrict',val);
        }
    },
    keywords:{
        get(){
            return this.$store.state.keywords;
        },
        set(val){
            this.$store.commit('setKeywords',val);
        }
    },
    showModel:{
        get(){
            return this.$store.state.showModel;
        },
        set(val){
            this.$store.commit('setshowModal',val);
        }
    },
    infoBoxSid:{
        get(){
            return this.$store.state.infoBoxSid;
        },
        set(val){
            this.$store.commit('setinfoBoxSid',val);
        }
    },
    ...mapGetters(['cityList','districtList','filteredStores']),
    ...mapState(['stores'])
},
methods:{
    keywordHighlight(val){
        return val.replace(new RegExp(this.keywords,'g'),`<span class="heighLight">${this.keywords}</span>`);
    },
    openInfo(sid){
        this.showModel = true;
        this.infoBoxSid = sid;
    }
},
watch: {
    districtList(v) {
      const [arr] = v;
      this.currDistrict = arr.name;
    },
  },
updated(){
    // console.log()
}
}
</script>

<style lang="scss">
.heighLight{
    color: #f08d49;
}
</style>